<template>
  <section>
    <div class="navbar-container box-shadow">
      <div class="container-fluid navbar-header flex">
        <span class="page-title">报警联系人管理</span>
      </div>
    </div>
    <el-tabs type="border-card" v-model="activeTab" @tab-click="handleClick">
      <el-tab-pane label="报警联系人" name="contact_person">
        <router-view  v-if="activeTab === 'contact_person'" name="contact_person"></router-view>
      </el-tab-pane>
      <el-tab-pane label="报警联系组" name="contact_group">
        <router-view  v-if="activeTab === 'contact_group'" name="contact_group"></router-view>
      </el-tab-pane>
    </el-tabs>
  </section>
</template>

<script>
export default {
  name: 'alarm_contact',
  data() {
    return {
      activeTab: 'contact_person'
    }
  },
  methods: {
    handleClick () {
      this.$router.push({name: 'view_contact_detail', params: { tap: this.activeTab}})
    }
  },
  mounted() {
    this.activeTab = this.$route.params.tap;
    if (!this.activeTab) {this.activeTab = 'contact_person'}
    this.handleClick()
  }
}
</script>

